}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
  font-family: 'Jim Nightshade', 'Libre Baskerville', 'Times New Roman', Times !important;
}
.heading {
  font-weight: 500;
}
.subheading {
  font-weight: 700;
}
.App {
  text-align: left;
}



header {
  
  color: white;
  text-align: center;
  

  padding-top: 5px;
  height: 125px;
  margin: 10px;
  
  background-color: rgb(49, 49, 49);
  box-shadow: 
    0 0 1px 2px #5f5c42,
    0 0px 10px 1px rgb(8, 8, 8);
}

header h4 {
  font-size: 50px;
  text-shadow: 1px 2px 2px black;
  
  font-weight: 200 !important;
}









.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



select option {
    color:black;
     
}

.civMenu {
    border-radius: 0px !important;
    margin: 10px;
    color: black !important;
    width: 275px;
    box-shadow: 
    0 0 1px 2px #5f5c42,
    0 0px 10px 1px rgb(8, 8, 8);

}

.civ-title {
    color: black;
    text-shadow: 1px 1px 2px gold;
    text-shadow: 1px 2px 1px white;
    font-weight: 1000;
    text-align: center;
    font-size: 35px;
    
}
.info {
    /* this makes civ info panel float when scrolling donw */
    /* float: left;
    position: fixed; */
    width: 275px;
    height: 400px;
    margin: 10px;
    margin-top: 5px;
    padding-left: 10px;
    -webkit-transition:all .25s ease;
    transition:all .25s ease;
    opacity:1;
    box-shadow: 
    0 0 1px 2px #5f5c42,
    0 0px 10px 1px rgb(8, 8, 8);
}

.info-text {
    font-family: 'Libre Baskerville', cursive;   
    font-size: 12px;
    font-weight: 600;
    color: black;
    text-shadow: 2px 2px 10px white;
    
}

.civPanel {
    float: left;

}




.infoline {
    color: white;
    
    
}

.btn-dark {
    background-color: rgb(66, 66, 66)  !important;
}

.btn-dark:hover {
    background-color: rgb(214, 214, 214) !important;
}



.x-overlay{
    position: absolute;
    width: 35px !important;
    height: 35px !important;
    opacity: 0.6;
}
.u-tooltip {
    width: 500px !important;
    position: static;
    
}

.u-tooltip .rc-tooltip-inner {
    font-family: 'Libre Baskerville', cursive; 
    font-size: 12px;
    font-weight: lighter;
    background-color: rgb(0, 0, 0);
    opacity: 1;
    color: white;
    border-radius: 0;  
    box-shadow: 
    0 0 1px 2px #5f5c42,
    0 0px 10px 1px rgb(8, 8, 8);
    
    
}

.u-tooltip .rc-tooltip-inner h6 {
    color: gold;
    text-shadow: 1px 1px 10px gold;
}

.u-tooltip .rc-tooltip-inner .stats {
    color: rgb(255, 241, 164);
    text-shadow: 1px 1px 10px gold;
}

.headingtest {
    color: black;
    background-color: white;
    width: 100%;
    height: 90px;
    
}
.panel-btn {
    border-color: black !important;
    background-color: rgb(29, 29, 29) !important;
    color: gold !important;
    border-radius: 0px !important;
    width: 330px;
    text-align: left !important;
}

.panel-btn:hover {
    background-color: rgb(212, 212, 212) !important;
}
.panel {
    color: rgb(116, 115, 109) !important;
    margin: 10px;
    padding: 0px;
    float: left;
    width: 330px;
    box-shadow: 
        0 0 1px 2px #5f5c42,
        0 0px 10px 2px rgb(8, 8, 8);
}

.panel:hover {
    
    background-color: black !important;
    box-shadow: 
        0 0 1px 2px #5f5c42,
        0 0px 10px 2px rgb(8, 8, 8);
}

.whitespace {
    opacity: 0.0;
}
.tree img {
    width: 40px;
    height: 40px;
}

.unit {
    /* border-top: 1px solid white; */
    width: 35px !important;
    height: 35px !important;
    margin-bottom: 5px;
    margin-right: 5px;
    box-shadow: 
            0 0 1px 2px #5f5c42;
            
    /* box-shadow: 
            0 0 1px 2px #5f5c42;  
            0 0px 15px 3px rgb(255, 247, 178); */  
}

.unit:hover {

    outline: 2px solid rgb(216, 215, 215);
    box-shadow: 
            0 0 6px 2px grey;
    
}





.unit-fu {
    outline: 2px solid rgb(255, 188, 45);
    box-shadow: 
            0 0 3px 1px rgb(223, 194, 64);

    /* box-shadow: 
            0 0 1px 1px rgb(179, 174, 150),
            0 0 6px 3px rgb(255, 189, 65); */
}

.unit-na {
    opacity: 0.2;
    box-shadow: none; 
}

.unit-na:hover {
    outline: none !important;
    box-shadow: none !important; 
            
}
.age {
    border-radius: 25px !important;
    width: 30px !important;
    height: 30px !important;
    margin-right: 10px;
    opacity: 0.7;
    border-radius: 0px;
    box-shadow: 
            0 0 1px 2px #5f5c42  ,
            0 0px 5px 1px rgb(255, 217, 113);
}

.age-empty {
    opacity: 0.0 !important;
}

.panel:focus {
    width: 800px;
}



.panel-space {
   
    font-size: 12px;
    color: white;
    
    padding: 5px;
    background-color: rgb(66, 66, 66)  !important;
}


.img {
    
    outline: 5px solid grey;
    outline-style: ridge;
    /* box-shadow: 
            0 0 5px 1px #ffe600  ,
            0 0 5px 1px rgb(255, 255, 255); */
}

.build-name {
    font-size: 25px;
    color: gold;
    text-shadow: -1px -1px 1px #aaa,
                     0px 1px 5px black,
                     0px 1px 5px rgba(0,0,0,0.7)
                     
}


.down-arrow {
    text-align: right !important;
    justify-content: right;
}
